a{
    display: block;
}
.glasses-selection-section{
    display: flex;
    justify-content: center;
}
.glasses-selection-section a{
    font-size: 42px;
    color: black;
}
.glasses-selection-section li{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 24px 117px;
    width: 462px;
    box-sizing: border-box;
    border: 1px solid #DCDCDC;
}
.glasses-selection-section li:first-child{
    border-bottom: 16px solid #AA0601;
}

@media (max-width:992px){
    .glasses-selection-section{
        li{
            width: 33.33%;
            padding: 16px 51px;
        }
        img{
            width: 32px;
            height: 32px;
        }
       a{
           font-size: 28px;
       }
       li:first-child{
        border-bottom: 8px solid #AA0601;
       }
    }
}
@media (max-width:767px){
    .glasses-selection-section{
        li{
            padding: 16px 51px;
        }
        img{
            width: 14px;
            height: 14px;
        }
       a{
           font-size: 14px;
       }
       li:first-child{
        border-bottom: 4px solid #AA0601;
       }
    }
}

.page2-banner{
    display: flex;
    justify-content: space-between;
    li{
        height: 309px;
        width: 49%;
    }
    li:first-child{
        background: url(https://hexschool.github.io/webLayoutTraining1st/week3/banner-1.png) no-repeat center;
        background-size: cover;
    }
    li:nth-child(2){
        background: url(https://hexschool.github.io/webLayoutTraining1st/week3/banner-2.png) no-repeat center;
        background-size: cover;
    }
}

@media (max-width:992px){
    .page2-banner{
        justify-content: center;
        li{
            width: 50% 
        }
    }
}
@media (max-width:767px){
    .page2-banner{
        li{
            height: 144px;
        }
    }
}

.product-title-h2{
    margin-top: 88px;
    margin-bottom: 10px;
    font-size: 42px;
    text-align: center;
}
.product-title-h3{
    margin-bottom: 40px;
    font-size: 32px;
    text-align: center;
}
.product-list{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    font-family: "Roboto", sans-serif;
    li{
        width: 22%;
        margin-bottom: 70px;
    }
}
.product-detail{
    display: flex;
    justify-content: space-between;
    margin-top: 8px;
    margin-bottom: 8px;
}
.product-name{
    font-size: 24px;
    text-transform: uppercase;
}
.product-price{
    font-size: 24px;
    text-transform: uppercase;
    color: #AA0601;
}
.product-color{
    display: flex;
    li{
        width: 24px;
        height: 24px;
    }
    li:first-child{
        background-color: #5F3E2D;
        border-radius:4px;
        margin-right: 8px;
    }
    li:nth-child(2){
        background-color: #B75929;
        border-radius:4px;
    }
}

.product-page{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 150px;
    
    li{
        padding: 10px 17px;
        border: 1px solid black;
        margin:0px -1px -1px 0px;
    }
    a{
        font-size: 18px;
        color: black;
    }
}

@media (max-width:992px){
    .product-list li{
        width: 44%;
    }
    .product-color li{
        width: 24px;
        height: 24px;
    }
}
@media (max-width:767px){
    .product-list li{
        width: 92%;
    }
    .product-color li{
        width: 16px;
        height: 16px;
    }
}